/* frontend/src/index.css */
@tailwind base;
@tailwind components;
@tailwind utilities;

/* --- Custom Styles from Prototype --- */
/* Use Inter font */
body {
    font-family: 'Inter', sans-serif;
    background-color: #030712; /* A very dark gray/black */
}

/* Custom styles for Tailwind toggle (override) */
.toggle-checkbox:checked {
    right: 0;
    border-color: #4ade80; /* green-400 */
}
.toggle-checkbox:checked + .toggle-label {
    background-color: #4ade80; /* green-400 */
}

/* infotip styles */
.infotip {
    position: relative;
    display: inline-block;
}

.infotip .infotiptext {
    visibility: hidden;
    width: 220px;
    background-color: #374151; /* gray-700 */
    color: #fff;
    text-align: left;
    border-radius: 6px;
    padding: 8px;
    position: absolute;
    z-index: 1;
    bottom: 125%;
    left: 50%;
    margin-left: -110px; /* Center the infotip */
    opacity: 0;
    transition: opacity 0.3s;
    font-size: 0.875rem;
    line-height: 1.25rem;
}

.infotip .infotiptext::after {
    content: "";
    position: absolute;
    top: 100%;
    left: 50%;
    margin-left: -5px;
    border-width: 5px;
    border-style: solid;
    border-color: #374151 transparent transparent transparent;
}

.infotip:hover .infotiptext {
    visibility: visible;
    opacity: 1;
}

/* Modal styles */
.modal-overlay {
    visibility: hidden;
    opacity: 0;
    position: fixed;
    inset: 0;
    background-color: rgba(0, 0, 0, 0.75);
    transition: all 0.3s ease-in-out;
    z-index: 40;
}
.modal-overlay.open {
    visibility: visible;
    opacity: 1;
}
.modal-box {
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%) scale(0.9);
    visibility: hidden;
    opacity: 0;
    background-color: #1f2937; /* gray-800 */
    color: #d1d5db; /* gray-300 */
    border: 1px solid #374151; /* gray-700 */
    border-radius: 0.75rem; /* rounded-xl */
    padding: 24px;
    width: 90%;
    max-width: 500px;
    z-index: 50;
    transition: all 0.3s ease-in-out;
}
.modal-overlay.open .modal-box {
    visibility: visible;
    opacity: 1;
    transform: translate(-50%, -50%) scale(1);
}

/* Fade-out animation */
@keyframes fadeOut {
    from { opacity: 1; }
    to { opacity: 0; transform: scale(0.95); }
}
.fade-out {
    animation: fadeOut 0.3s ease-out forwards;
}